import React, { useState, useEffect } from 'react'
import {
  ThemeProvider,
  createMuiTheme,
  makeStyles,
} from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container'
import CssBaseline from '@material-ui/core/CssBaseline'
import Paper from '@material-ui/core/Paper'
import FormGroup from '@material-ui/core/FormGroup'
import FormLabel from '@material-ui/core/FormLabel'
import FormControl from '@material-ui/core/FormControl'
import Input from '@material-ui/core/Input'
import InputLabel from '@material-ui/core/InputLabel'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import IconButton from '@material-ui/core/IconButton'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'

import theme from './theme.json'

const muiTheme = createMuiTheme(theme)

const useStyles = makeStyles((theme) => ({
  mainPaper: {
    height: 434,
    width: 320,
    marginTop: 'calc(50% - 217px)',
    marginLeft: 'calc(50% - 160px)',
    padding: theme.spacing(3),
  },
  input: {
    marginBottom: theme.spacing(3),
  },
  label: {
    marginBottom: theme.spacing(4),
  },
  numberContainer: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
  },
  number: {
    padding: '5px 15px',
    color: 'rgba(255, 238, 113, 1)',
    border: '2px solid rgba(255, 238, 113, 0.5)',
    fontSize: '1.375rem',
    boxSizing: 'border-box',
    fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
    fontWeight: '500',
    lineHeight: '1.75',
    borderRadius: '8px',
    letterSpacing: '0.02857em',
    textTransform: 'uppercase',
    display: 'inline-flex',
  },
  logout: {
    position: 'fixed',
    top: '0.3em',
    right: '0.3em',
    width: 70,
    height: 70,
  },
}))

const Form = ({ token }) => {
  const classes = useStyles(muiTheme)
  const [id, setId] = useState('')
  const [currentId, setCurrentId] = useState(null)
  const [loading, setLoading] = useState(true)
  const [trigger, refetch] = useState(false)

  useEffect(() => {
    typeof window !== 'undefined' &&
      fetch(`${process.env.REACT_APP_SERVER_URL}/getLastCertificateId`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
        .then((response) => response.json())
        .then(({ lastId }) => {
          setCurrentId(lastId)
          setLoading(false)
        })
  }, [setCurrentId, setLoading, trigger, token])

  const handleSubmit = (certificateId) => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/setLastCertificateId`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ` + token,
      },
      body: JSON.stringify({
        certificateId,
      }),
    }).then(() => {
      setId('')
      refetch(!trigger)
    })
  }
  return (
    <Paper className={classes.mainPaper} elevation={5}>
      <Typography variant='h5' color='textSecondary' align='center'>
        Текущий последний номер сертификата
      </Typography>
      <div className={classes.numberContainer}>
        {!loading ? (
          <div className={classes.number}>{currentId}</div>
        ) : (
          <CircularProgress />
        )}
      </div>
      <FormGroup>
        <FormLabel
          className={classes.label}
          children={
            <Typography variant='h5' color='textSecondary' align='center'>
              Установить последний номер сертификата
            </Typography>
          }
        />
        <FormControl className={classes.input}>
          <InputLabel>Номер</InputLabel>
          <Input
            value={id}
            type='number'
            onChange={(e) => setId(e.target.value)}
          />
        </FormControl>
        <FormControl>
          <Button
            disabled={!id}
            color='primary'
            variant='contained'
            onClick={() => handleSubmit(id)}
          >
            Установить
          </Button>
        </FormControl>
      </FormGroup>
    </Paper>
  )
}

const Auth = ({ refetch, trigger }) => {
  const classes = useStyles(muiTheme)

  const [password, setPassword] = useState('')
  const [error, setError] = useState(false)
  console.log(error)

  const handleSubmit = (password) => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        password,
      }),
    })
      .catch((err) => console.log(err))
      .then((response) => response.json())
      .then(({ token, message }) => {
        message === 'Invalid password.' && setError(true)
        token && window.localStorage.setItem('token', token)
        token && refetch(!trigger)
      })
  }
  return (
    <Dialog maxWidth='xs' fullWidth open={true}>
      <DialogTitle>Введите пароль</DialogTitle>
      <DialogContent>
        <FormGroup>
          <FormControl className={classes.input}>
            <InputLabel error={error}>
              {!error ? 'Пароль' : 'Неверный пароль'}
            </InputLabel>
            <Input
              value={password}
              type='text'
              onChange={(e) => {
                setPassword(e.target.value)
                setError(false)
              }}
              error={error}
            />
          </FormControl>
        </FormGroup>
        <DialogActions>
          <FormControl>
            <Button
              disabled={!password}
              color='primary'
              variant='outlined'
              onClick={() => handleSubmit(password)}
            >
              Войти
            </Button>
          </FormControl>
        </DialogActions>
      </DialogContent>
    </Dialog>
  )
}

const App = () => {
  const classes = useStyles(muiTheme)
  const [loading, setLoading] = useState(true)
  const [authorized, authorize] = useState(false)
  const [trigger, refetch] = useState(false)
  const token = window?.localStorage.getItem('token')

  useEffect(() => {
    typeof window !== 'undefined' &&
      fetch(`${process.env.REACT_APP_SERVER_URL}/auth`, {
        headers: {
          Authorization: `Bearer ` + token,
        },
      })
        .catch(() => {
          authorize(false)
          setLoading(false)
        })
        .then((response) => response.json())
        .then(({ status }) => {
          status === 'Succes' && authorize(true)
          setLoading(false)
        })
  }, [token, authorize, trigger])
  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />
      <IconButton
        className={classes.logout}
        color='primary'
        onClick={() => {
          window.localStorage.removeItem('token')
          authorize(false)
          refetch(!trigger)
        }}
      >
        <ExitToAppIcon fontSize='large' />
      </IconButton>
      {!loading ? (
        <Container style={{ height: '720px' }} maxWidth='md'>
          {authorized ? (
            <Form token={token} />
          ) : (
            <Auth refetch={refetch} trigger={trigger} />
          )}
        </Container>
      ) : (
        <CircularProgress
          style={{
            height: '80px',
            marginTop: 'calc(50% - 40px)',
            width: '80px',
            marginLeft: 'calc(50% - 40px)',
          }}
        />
      )}
    </ThemeProvider>
  )
}

export default App
